"use client";

import Link from "next/link";
import Image from "next/image";
import { useState, useEffect } from "react";

import logo from "../../public/logo-sef.png";

// CSS
import "../../styles/navbar/navbar-Home.scss";

export default function NavbarHome() {
  const [activeMenu, setActiveMenu] = useState(null);

  // Fecha qualquer menu quando clicar fora
  useEffect(() => {
    const closeAll = () => setActiveMenu(null);
    document.addEventListener("click", closeAll);
    return () => document.removeEventListener("click", closeAll);
  }, []);

  // Evita que o clique interno feche tudo
  const stopPropagation = (e) => e.stopPropagation();

  // Ativa / desativa o menu e o ghost-icon
  const toggleMenu = (menuId) => {
    setActiveMenu((prev) => (prev === menuId ? null : menuId));
  };

  return (
    <nav id="nbHome" className="navbar navbar-expand-sm sticky-top z-2" onClick={stopPropagation}>
      <div className="container-fluid">

        {/* Logo */}
        <Link href="/" className="navbar-brand">
          <Image className="img-fluid logoNavbar" src={logo} alt="Logo" priority />
        </Link>

        {/* Botão Sanduíche */}
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarHome"
          aria-controls="navbarHome"
          aria-expanded="false"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        {/* Menu */}
        <div className="collapse navbar-collapse justify-content-end me-0" id="navbarHome">
          <ul className="navbar-nav">

            {/* SEARCH (com ghost-icon) */}
            <li className="nav-item dropdown mobile-ghost-wrap">
              <button
                id="SearchBtn"
                className={`nav-link nav-icon mobile-ghost-btn ${activeMenu === "SearchBtn" ? "active" : ""}`}
                onClick={() => toggleMenu("SearchBtn")}
              >
                <span className="iconMove">
                  <img src="/icons/search.svg" alt="Buscar" />
                </span>
              </button>

              {/* ghost-icon */}
              {activeMenu === "SearchBtn" && (
                <div className="ghost-indicator">
                  <img src="/icons/search.svg" alt="Buscar" />
                </div>
              )}

              <ul className="dropdown-menu dropdown-menu-end search-dropdown">
                <li className="px-3 py-2">
                  <input className="form-control search-input" placeholder="Buscar..." type="search" />
                </li>
              </ul>
            </li>

            {/* Serviços */}
            <li className="nav-item dropdown mobile-ghost-wrap">
              <button
                id="Services"
                className={`nav-link mobile-ghost-btn ${activeMenu === "Services" ? "active" : ""}`}
                onClick={() => toggleMenu("Services")}
              >
                <img src="/icons/layers.svg" alt="Serviços" />
              </button>

              {activeMenu === "Services" && (
                <div className="ghost-indicator">
                  <img src="/icons/layers.svg" alt="Serviços" />
                </div>
              )}

              <ul className="dropdown-menu dropdown-menu-end">
                <li><Link className="dropdown-item" href="/loja"><i className="bi bi-bag"></i> Loja</Link></li>
                <li><Link className="dropdown-item" href="/orcamento"><i className="bi bi-pencil-square"></i> Orçamento</Link></li>
                <li><Link className="dropdown-item" href="/dev"><i className="bi bi-code-slash"></i> Desenvolvedor</Link></li>
                <li><Link className="dropdown-item" href="/atualizacoes"><i className="bi bi-arrow-down-up"></i> Atualizações</Link></li>
                <li><Link className="dropdown-item" href="/area-de-atuacao"><i className="bi bi-geo-alt"></i> Área de atuação</Link></li>
                <li><Link className="dropdown-item" href="/termos"><i className="bi bi-file-earmark"></i> Termos de Serviços</Link></li>
              </ul>
            </li>

            {/* Quem Somos */}
            <li className="nav-item dropdown mobile-ghost-wrap">
              <button
                id="Qmsm"
                className={`nav-link mobile-ghost-btn ${activeMenu === "Qmsm" ? "active" : ""}`}
                onClick={() => toggleMenu("Qmsm")}
              >
                <img src="/icons/handshake.svg" alt="Quem Somos" />
              </button>

              {activeMenu === "Qmsm" && (
                <div className="ghost-indicator">
                  <img src="/icons/handshake.svg" alt="Quem Somos" />
                </div>
              )}

              <ul className="dropdown-menu dropdown-menu-end">
                <li><Link className="dropdown-item" href="/parceiro"><i className="bi bi-people"></i> Parceiro</Link></li>
                <li><Link className="dropdown-item" href="/depoimentos"><i className="bi bi-stars"></i> Depoimentos</Link></li>
                <li><Link className="dropdown-item" href="/creditos"><i className="bi bi-patch-check"></i> Créditos</Link></li>
                <li><Link className="dropdown-item" href="/sobre"><i className="bi bi-info-lg"></i> Sobre</Link></li>
              </ul>
            </li>

            {/* Ajuda & Suporte */}
            <li className="nav-item dropdown mobile-ghost-wrap">
              <button
                id="Ajsu"
                className={`nav-link mobile-ghost-btn ${activeMenu === "Ajsu" ? "active" : ""}`}
                onClick={() => toggleMenu("Ajsu")}
              >
                <img src="/icons/send.svg" alt="Ajuda e Suporte" />
              </button>

              {activeMenu === "Ajsu" && (
                <div className="ghost-indicator">
                  <img src="/icons/send.svg" alt="Ajuda" />
                </div>
              )}

              <ul className="dropdown-menu dropdown-menu-end">
                <li><Link className="dropdown-item" href="/dicas"><i className="bi bi-lightbulb"></i> Dicas</Link></li>
                <li><Link className="dropdown-item" href="/contato"><i className="bi bi-person"></i> Contato</Link></li>
                <li><Link className="dropdown-item" href="/avaliacao"><i className="bi bi-google"></i> Google</Link></li>
                <li><Link className="dropdown-item" href="/faq"><i className="bi bi-question-circle"></i> FAQ</Link></li>
                <li><Link className="dropdown-item" href="/privacidade"><i className="bi bi-shield-lock"></i> Privacidade</Link></li>
              </ul>
            </li>

            {/* Portfólio */}
            <li className="nav-item dropdown mobile-ghost-wrap">
              <button
                id="PortCert"
                className={`nav-link mobile-ghost-btn ${activeMenu === "PortCert" ? "active" : ""}`}
                onClick={() => toggleMenu("PortCert")}
              >
                <img src="/icons/briefcase.svg" alt="Portfólio" />
              </button>

              {activeMenu === "PortCert" && (
                <div className="ghost-indicator">
                  <img src="/icons/briefcase.svg" alt="Portfólio" />
                </div>
              )}

              <ul className="dropdown-menu dropdown-menu-end">
                <li><Link className="dropdown-item" href="/feedback"><i className="bi bi-chat-square"></i> Feedback</Link></li>
                <li><Link className="dropdown-item" href="/certificacoes"><i className="bi bi-award"></i> Certificações</Link></li>
                <li><Link className="dropdown-item" href="/galeria"><i className="bi bi-card-image"></i> Galeria</Link></li>
                <li><Link className="dropdown-item" href="/antes-depois"><i className="bi bi-camera"></i> Antes e Depois</Link></li>
              </ul>
            </li>

          </ul>
        </div>
      </div>
    </nav>
  );
}